<template>
	<div id="history-receipts">
		<!--顶部菜单-->
		<nav-bar class="history-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				历史单据
			</div>
			<div slot="right">
				筛选
			</div>
		</nav-bar>
		<top-banner-navs @customerClick="customerClick" :banner-navs-arr="topBannerNav" class="banner"/>
		<div class="timeBoxChange" v-show="isShow">{{showTime}}</div>
		<!--滚动-->
		<scroll class="history-scroller"
				ref="scroller"
				@bscroll="bscroll"
				:probe-type=3
				@pullingup="loadMove"
		>
			<receipts-content :order-content-arr="OrderContentArr" ref="receiptsContent"/>
		</scroll>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import TopBannerNavs from 'components/common/topbannernavs/TopBannerNavs'
import Scroll from 'components/common/scroll/Scroll'

import ReceiptsContent from './childComps/ReceiptsContent'
import {getHistoryReceipts} from 'network/order'

export default {
  name: 'HistoryReceipts',
	components: {
		NavBar,
		TopBannerNavs,
		ReceiptsContent,
		Scroll
	},
	methods:{
		//返回按钮
		backClick(){
			this.$router.go(-1)
		},
			//客户和供应商的点击
		customerClick(index){
			switch(index){
				case 0:
					this.currentType="allOrders"
					break
				case 1:
					this.currentType="pending"
					
					break
				case 2:
					this.currentType="contract"
					break
				case 3:
					this.currentType="success"
					break
			}		
				//this.$refs.scroller.scroll.refresh()
				//console.log(this.currentType)	)
		},
		//加载更多
		loadMove(){
			console.log("加载更多")
		},
		//滚动位置监听
		bscroll(position){
			let y=-position.y
			let orderAllDate=this.orderAllDate[this.currentType]
			this.isShow=-position.y>1
			for(let i=-1;i<orderAllDate.length;i++){
				if(y>this.scllorShowArr[this.currentType][i] && y<this.scllorShowArr[this.currentType][i+1]){
					this.posIndex[this.currentType]=i
				}
			}
		},
		//储存和设置当前选中组件的高度(这个是获取组件高度 问题：换了内容获但取不了组件的高度)
		/*setCurrentComHeight(){
			//获取子组件里的div的组件高度
			let timedivs=this.$refs.receiptsContent.$refs.timediv
			for(let o of timedivs){
				this.scllorShowArr[this.currentType].push(o.offsetTop)
			}
			//获取选中数组的时间放进数组
			this.orderAllArr[this.currentType].forEach(obj=>{
				this.orderAllDate[this.currentType].push(obj.time)
			})
			this.scllorShowArr[this.currentType].push(1000000)
			console.log(this.scllorShowArr[this.currentType])
		},*/
		//获取并且计算对象里数组的个数
		getAndMatchComHeight(type){
			
			let itemBoxHeight=179					//每个盒子的高度179/(获取this.$refs.****.offsetTop)
			
			let itemShowBoxHeight=42				//一个显示时间的框的高度为42
			
			let oaa=this.orderAllArr[type]			//对象的当前类型
			
			let length=oaa.length					//对象的长度
			
			this.scllorShowArr[type].push(0)		//先给数组储存一个0 因为起点最顶部
			
			for(let i=0;i<length;i++){				//循环list的对象个数
				//list的个数 代表一个item
				//高度数组
				this.scllorShowArr[type].push(oaa[i].list.length*itemBoxHeight+itemShowBoxHeight+this.scllorShowArr[type][i])
				
				//时间数组
				this.orderAllDate[type].push(oaa[i].time)							
			}
			//console.log(this.scllorShowArr[type])
		},
		//获取数据
		getHistoryReceipts(type,status){
			let page=1;
			this.orderAllArr[type]=[];
			getHistoryReceipts(type,page,status).then(res=>{
				this.orderAllArr[type].push(...res)
				this.getAndSetTime(type)						//获取并且设置时间
			})
		},
		//把获取的时间放进去数组
		getAndSetTime(type){	
			let oaa=this.orderAllArr[type]					//当前选中的类型
			let itemBoxHeight=179							//一个盒子的高度需要计算
			let itemShowBoxHeight=42						//显示的高度需要计算
			//这里可以读取-其他地方读取不了
			for(let o in this.orderAllArr[type]){			//循环
				//console.log(this.orderAllArr[type][o]['time'])
				this.scllorShowArr[type].push(oaa[o].list.length*itemBoxHeight+itemShowBoxHeight+this.scllorShowArr[type][o])
				this.orderAllDate[type].push(this.orderAllArr[type][o]['time'])
			}
		}
	},
	data(){
		return{
			topBannerNav:[
				'入库单','出库单','盘点单','已完成'
			],
			scllorShowArr:{					//时间显示高度数组[0,221....]
				allOrders:[],
				pending:[],
				contract:[],
				success:[],
			},				
			isShow:false,					//是否显示副本时间
			currentType:"allOrders",		//当前选中
			orderAllDate:{					//保存当前选中列表的时间[]
				allOrders:[],
				pending:[],
				contract:[],
				success:[],
			},				
			posIndex:{						//当前时间显示哪一个
				allOrders:0,
				pending:0,
				contract:0,
				success:0,
			},
			//this.orderAllArr[allOrders].list
			orderAllArr:{
				allOrders:[
				],
				pending:[],
				contract:[],
				success:[],
			},
		}
	},
	mounted(){
		//问题-这里保存的是上一个点击页面的组件高度（解决办法1 list的个数*一个list[0]的高度在加上当前的一个高度）
		//this.setCurrentComHeight()	
		//开始先保存各个对象的高度
		for(let oName in this.orderAllArr){
			this.getAndMatchComHeight(oName)
		}
		//同上效果
		/*this.getAndMatchComHeight('allOrders')
		this.getAndMatchComHeight('pending')
		this.getAndMatchComHeight('contract')
		this.getAndMatchComHeight('success')*/
	},
	//计算属性
	computed:{
		OrderContentArr(){
			return this.orderAllArr[this.currentType]
		},
		//显示时间
		showTime(){
			return this.orderAllDate[this.currentType][this.posIndex[this.currentType]]
		}
	},
	created(){
		//0出库，1入库，2盘点，3已完成
		//获取数据
		this.getHistoryReceipts('allOrders',0)
		this.getHistoryReceipts('pending',1)
		this.getHistoryReceipts('contract',2)
		this.getHistoryReceipts('success',3)
	},
}
</script >

<style spoced>
.history-nav-bar{
	background:#ff5959;
	color:#fff;
}
.banner{
	background:#fff;
}
  .icon-left{
	text-align:left;
	position:relative;
  }
  .icon-left img{
	position:absolute;
	top:10px;
	left:10px;
  }
#history-receipts{
	background:#f3f3f3;
    height: 100vh;
    position: relative;
	z-index:11;
}

.history-scroller{
    /*height:300px;*/
    position: absolute;
	height:calc(100% - 89px);
    right: 0;
    left: 0;
	overflow:hidden;
  }
.timeBoxChange{
	z-index:13;
	position:fixed;
	background:#f3f3f3;
	width:100%;
	padding:12px;
	color:red;
}
</style>

